import React, { Suspense } from 'react'
import {
  Redirect,
  Route,
  Switch
} from 'react-router-dom'
import { CContainer, CFade } from '@coreui/react'

// routes config
// import routes from '../routes'

import modules from '../modules';
  
const loading = (
  <div className="pt-3 text-center">
    <div className="sk-spinner sk-spinner-pulse"></div>
  </div>
)

const TheContent = (_props) => {
  let routeView = [];
  modules.forEach((module) => {
    if(module.routeProps._children && module.routeProps._children.length > 0) {
      module.routeProps._children.forEach((data) => {
        routeView.push({
          path: data.path,
          name: data.name,
          component: data.component
        });
      });
    }
    routeView.push({
      path: module.routeProps.path,
      name: module.routeProps.name,
      component: module.routeProps.component
    });
  });
  
  return (
    <main className="c-main">
      <CContainer fluid>
        <Suspense fallback={loading}>
          <Switch>
            {
              routeView.map((module, idx) => {
              return module.component && (
                <Route
                  key={idx}
                  path={module.path}
                  //exact={route.exact}
                  name={module.name}
                  render={props => (
                    <CFade>
                      <module.component {...props} auth={_props.auth} loading={_props.loading} modal={_props.modal} />
                    </CFade>
                  )} />
              )
            })
            }
            <Redirect from="/" to="/dashboard" />
          </Switch>
        </Suspense>
      </CContainer>
    </main>
  )
}

export default React.memo(TheContent)
