import React from 'react';

const List = React.lazy(() => import("./components/ListStation"));

const Index = {
    routeProps: {
        path: '/station',
        name: 'Stasiun',
        _tag: 'CSidebarNavDropdown',
        icon: 'cil-bank',
        _children: [
            {
                path: '/station/list',
                // exact: true,
                component: List,
                name: 'Daftar Stasiun',
                _tag: 'CSidebarNavItem',
            }
        ]
    },
};

export default Index;