import React from 'react';

const Dashboard1 = React.lazy(() => import("./components/Dashboard1"));

const Index = {
    routeProps: {
        path: '/metabase',
        name: 'Metabase',
        _tag: 'CSidebarNavDropdown',
        icon: 'cil-bank',
        _children: [
            {
                path: '/metabase/dashboard1',
                // exact: true,
                component: Dashboard1,
                name: 'Dashboard 1',
                _tag: 'CSidebarNavItem',
            }
        ]
    },
};

export default Index;