import React from 'react'
import { useSelector, useDispatch } from 'react-redux'
import {
  CCreateElement,
  CSidebar,
  CSidebarBrand,
  CSidebarNav,
  CSidebarNavDivider,
  CSidebarNavTitle,
  CSidebarMinimizer,
  CSidebarNavDropdown,
  CSidebarNavItem,
  CImg
} from '@coreui/react'

// sidebar nav config
// import navigation from './_nav'

import modules from '../modules'

const TheSidebar = () => {
  const dispatch = useDispatch()
  const show = useSelector(state => state.sidebarShow)

  var navigation = [];
  modules.forEach((module)=>{
    if(module.routeProps){
      // TODO use simple algorithm
      // module.navProps.name = module.routeProps.name;
      // module.routeProps.to = module.routeProps.path;
      let m = {
        _tag: module.routeProps._tag,
        name: module.routeProps.name,
        icon: module.routeProps.icon,
        badge: module.routeProps.badge
      }
      if(m._tag === 'CSidebarNavDropdown') {
        let _children = []
        module.routeProps._children.forEach((data) => {
          _children.push({
            _tag: data._tag,
            name: data.name,
            to: data.path
          });
        });
        m.route = module.routeProps.path;
        m._children = _children;
      } else {
        m.to = module.routeProps.path;
      }
      navigation.push(m);
    }
  });

  return (
    <CSidebar
      show={show}
      onShowChange={(val) => dispatch({type: 'set', sidebarShow: val })}
    >
      <CSidebarBrand className="d-md-down-none" to="/">
        {/* <CIcon
          className="c-sidebar-brand-full"
          name="logo-negative"
          height={35}
        /> */}
        <CImg
            src={'/logo/smartstation.svg'}
            className="c-sidebar-brand-full"
            alt="Logo Viana"
            height={35}
          />
        {/* <CIcon
          className="c-sidebar-brand-minimized"
          name="sygnet"
          height={35}
        /> */}
        <CImg
            src={'/logo/smartstation-mini.svg'}
            className="c-sidebar-brand-minimized"
            alt="Logo Viana"
            height={35}
          />
      </CSidebarBrand>
      <CSidebarNav>

        <CCreateElement
          items={navigation}
          components={{
            CSidebarNavDivider,
            CSidebarNavDropdown,
            CSidebarNavItem,
            CSidebarNavTitle
          }}
        />
      </CSidebarNav>
      <CSidebarMinimizer className="c-d-md-down-none"/>
    </CSidebar>
  )
}

export default React.memo(TheSidebar)
